import FEATURE_TOGGLE from '../../src/feature-toggle';

class LineUtils {

  /*
   *  Given a vector will return: { endpoint, slope, intercept }
   *  1- Find missing segment points adding velocity to location on
   *     both vectors.
   *  2- Find the slope value for both lines
   *  3- Find the y-intercept value for both lines.
   */
  vectorLineProps(v) {
    let e = v.location.add(v.velocity);
    let s = this.slope(v.location.getY(), e.getY(), v.location.getX(), e.getX());
    let i = this.yIntercept(e.getX(), e.getY(), s);
    return { endpoint: e, slope: s, intercept: i };
  }

  intersect(m1, b1, m2, b2) {
    let x = (b2-b1)/(m1-m2);
    let y = this.lineEq(x, m1, b1);
    return { x: x, y: y };
  }

  lineEq(x, m, b) {
    return m*x + b;
  }

  slope(y1, y2, x1, x2) {
    return (y1 - y2) / (x1 - x2);
  }

  yIntercept(x, y, m) {
    return y - m * x;
  }

  xIntercept(x, y, m) {
    return x - m / x;
  }
}

let instance = new LineUtils();

export default instance;
